/* App.css */
.container {
  display: flex;
  height: 100vh;
}

.left-panel {
  width: 60%; 
  padding: 20px;
  background-color: #f8f9fa;
  border-right: 1px solid #ccc;
  overflow-y: auto;
}

.right-panel {
  width: 40%; 
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  flex-direction: column;
}

.display-box {
  width: 200px; 
  height: 200px;
  border: 2px dashed #ccc;
  position: relative;
}

.selected-part {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.about-button {
  background-color: #ee8bba;
  color: white;
  border: none;
  position: fixed;
  top: 10px;
  right: 10px;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 4px;
}

.about-button:hover {
  background-color: #f4c2cd;
}

.save-button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #00a1d6;
  color: white;
  border: none;
  cursor: pointer;
  margin-top: 10px;
}

.save-button:hover {
  background-color: #63bad6;
}

@media (max-width: 768px) {
  .container {
    flex-direction: column;
  }

  .left-panel, .right-panel {
    width: 100%;
    height: 50%; 
    border-right: none; 
    border-bottom: 1px solid #ccc;
  }

  .display-box {
    width: 150px; 
    height: 150px;
  }
}
